.box {
  margin: 20px;
}
.img-box {
  width: 320px;
  //   max-height: 180px;
  //   overflow: hidden;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.gallery {
  display: flex;
  flex-wrap: wrap;

  width: 1020px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;

  list-style: none;
}
.gallery__item {
  box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%),
    1px 4px 6px rgb(0 0 0 / 16%);
  &:not(:nth-child(3n)) {
    margin-right: 30px;
  }
  &:not(:nth-last-child(-n + 3)) {
    margin-bottom: 30px;
  }

  transition: transform 250ms ease;
  &:hover,
  &:focus {
    transform: scale(1.05);
  }
}
.stats-item {
  display: flex;
  align-items: center;
  margin: 0;
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
.stats {
  padding: 20px;
}
.material-icons {
  margin-right: 10px;
}
